<template>
  <div class="main">
    <div class="topTool">
      <el-form :inline="true" :model="formInline" class="demo-form-inline" size="small">
        <el-form-item label="角色">
          <el-input v-model="formInline.roleName" placeholder="角色"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="formInline.description" placeholder="描述"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            v-if="button.selectRole===1"
            type="primary"
            icon="el-icon-search"
            @click="selectRole"
          >搜索</el-button>

          <el-button
            v-if="button.updateRole===1"
            type="primary"
            icon="el-icon-edit"
            @click="updateRole"
          >编辑</el-button>

          <el-button
            v-if="button.addRole===1"
            type="primary"
            icon="el-icon-share"
            @click="addRole"
          >新增</el-button>

          <el-button
            v-if="button.deleteRole===1"
            type="primary"
            icon="el-icon-delete"
            @click="deleteRole"
          >删除</el-button>

          <el-button type="primary">
            上传
            <i class="el-icon-upload el-icon--right"></i>
          </el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="flex-table">
      <div class="flex-table-wrap" style>
        <el-table
          v-loading="loading"
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%;height:100%;"
          border
          size="small"
          @selection-change="changeFun"
        >
          <el-table-column label="多选" type="selection" width="40px"></el-table-column>
          <el-table-column prop="roleId" label="ID" width="80px"></el-table-column>
          <el-table-column prop="roleName" label="姓名" width="150px"></el-table-column>
          <el-table-column prop="description" label="描述" width="150px"></el-table-column>
          <el-table-column prop="creationTime" label="创建时间" width="200px" show-overflow-tooltip></el-table-column>
        </el-table>
      </div>
    </div>
    <div class="bottomPagination">
      <div class="xd-pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          small
        ></el-pagination>
      </div>
    </div>
    <!-- 新增界面 -->
    <div v-if="isadd===1">
      <el-dialog title="新增角色" :visible.sync="dialogFormVisible" width="400px">
        <el-form ref="roleForm" :model="roleForm" :rules="roleFormRules" label-width="80px">
          <el-form-item label="ID" prop="roleId">
            <el-input v-model="roleForm.roleId" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="角色" prop="roleName">
            <el-input v-model="roleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="描述" prop="description">
            <el-input v-model="roleForm.description"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addRoleSubmit">添加</el-button>
            <el-button @click="cancel('roleForm')">取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
    <!-- 编辑页面 -->
    <div v-if="isadd===2">
      <el-dialog title="编辑角色" :visible.sync="dialogFormVisible">
        <el-form ref="roleUpdateForm" :model="roleForm" :rules="roleFormRules" label-width="80px">
          <el-form-item label="ID" prop="roleId">
            <el-input v-model="roleForm.roleId" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="角色" prop="roleName">
            <el-input v-model="roleForm.roleName"></el-input>
          </el-form-item>
          <el-form-item label="描述" prop="description">
            <el-input v-model="roleForm.description"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="updateRoleSubmit">提交</el-button>
            <el-button @click="cancel('roleUpdateForm')">取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>
<script>
export default {
  name: "SysExceptionLogList",
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      total: 0,
      tableData: [],
      loading: true,
      formInline: {
        roleName: "",
        description: ""
      },
      roleForm: {
        roleId: "",
        roleName: "",
        description: ""
      },
      roleFormRules: {
        roleId: [],
        roleName: [
          { required: true, message: "角色名称不能为空", trigger: "blur" }
        ],
        description: [
          { required: true, message: "描述名称不能为空", trigger: "blur" }
        ]
      },
      dialogFormVisible: false,
      isadd: 1,
      loading: true,
      multipleSelection: [],
      button: {
        selectRole: 1,
        addRole: 1,
        deleteRole: 1,
        updateRole: 1
      }
    };
  },
  computed: {},
  methods: {
    /* 每页显示数量变化 */
    handleSizeChange(val) {
      this.pageSize = val;
      this.selectRole();
    },
    /* 页码变化 */
    handleCurrentChange(val) {
      this.currentPage = val;
      this.selectRole();
    },
    /* 角色查询 */
    selectRole() {
      this.$ajax({
        method: "post",
        url: "selectRole",
        params: {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          roleName: this.formInline.roleName,
          description: this.formInline.description
        }
      })
        .then(res => {
          this.tableData = res.data.data;
          this.total = res.data.total;
          this.loading = false;
        })
        .catch(err => {
          alert(err);
        });
    },
    /* 点击更新按钮 */
    updateRole() {
      if (this.multipleSelection == 0) {
        alert("请选择需要编辑的行");
      } else if (this.multipleSelection.length >= 2) {
        alert("不能编辑多行");
      } else {
        (this.isadd = 2), (this.dialogFormVisible = true);
        this.roleForm = this.multipleSelection[0];
      }
    },
    /* 更新角色信息 */
    updateRoleSubmit() {
      this.$ajax({
        method: "post",
        url: "updateRole",
        data: this.roleForm
      })
        .then(res => {
          this.dialogFormVisible = false;
          this.selectRole();
          this.clearRoleForm();
        })
        .catch(err => {
          alert(err);
        });
    },
    /* 点击添加按钮 */
    addRole() {
      this.clearRoleForm();
      this.isadd = 1;
      this.dialogFormVisible = true;
    },

    /* 添加角色信息 */
    addRoleSubmit() {
      this.$ajax({
        method: "post",
        url: "addRole",
        data: this.roleForm
      })
        .then(res => {
          this.dialogFormVisible = false;
          this.selectRole();
          this.clearRoleForm();
        })
        .catch(err => {
          alert(err);
        });
    },
    /* 点击删除按钮 */
    deleteRole() {
      this.$ajax({
        method: "post",
        url: "deleteRole",
        data: this.multipleSelection
      })
        .then(res => {
          alert(res.data);
          this.selectRole();
        })
        .catch(err => {
          alert(err);
        });
    },
    /* 批量删除角色信息 */
    deleteRoleSubmit() {},

    /* 复选框改变时 */
    changeFun(val) {
      this.multipleSelection = val;
    },

    /* 清空表单信息 */
    clearRoleForm() {
      this.roleForm.roleId = "";
      this.roleForm.roleName = "";
      this.roleForm.description = "";
    },
    cancel(formName) {
      this.dialogFormVisible = false;
    }

    /** 获取按钮信息 */
  },

  mounted() {
    this.selectRole();
  }
};
</script>

